<template>
  <div class="swaps-inputs">
    <SwapsItemHeader
      v-model:filters="filters"
      v-model:perPage="perPage"
      :radio-buttons="radioButtons"
      :columns="columns"
    />
    <SwapsItemTable
      v-model:page="page"
      :data="data"
      :name="'inputsTable'"
      :columns="activeColumns"
      :per-page="perPage"
      :total-count="totalCount"
    />
  </div>
</template>

<script>
import { computed, reactive } from 'vue'
import { useGetters, useMutations } from '@/shared/plugins/vuex'
import { MODULES_NAMES } from '@/shared/common/modules'
import SwapsItemTable from '@/admin/views/Swaps/SwapsItem/SwapsItemTable'
import SwapsItemHeader from '@/admin/views/Swaps/SwapsItem/SwapsItemHeader'

export default {
  name: 'SwapsInputs',
  components: {
    SwapsItemTable,
    SwapsItemHeader
  },
  setup () {
    const {
      getInputsPage,
      getInputsFilters,
      getInputsPerPage,
      getInputsData: data,
      getInputsColumns: columns,
      getInputsTotalCount: totalCount,
      getInputsActiveColumns: activeColumns
    } = useGetters(MODULES_NAMES.SWAPS)
    const {
      SET_INPUTS_PAGE,
      SET_INPUTS_FILTERS,
      SET_INPUTS_PER_PAGE
    } = useMutations(MODULES_NAMES.SWAPS)

    const filters = computed({
      get: () => getInputsFilters.value,
      set: SET_INPUTS_FILTERS
    })

    const perPage = computed({
      get: () => getInputsPerPage.value,
      set: SET_INPUTS_PER_PAGE
    })
    const page = computed({
      get: () => getInputsPage.value,
      set: SET_INPUTS_PAGE
    })

    const radioButtons = reactive([
      { key: 'all', title: 'All' },
      { key: 'unconfirmed', title: 'Unconfirmed' },
      { key: 'confirmed', title: 'Confirmed' }
    ])

    return {
      data,
      page,
      perPage,
      filters,
      columns,
      totalCount,
      radioButtons,
      activeColumns
    }
  }
}
</script>

<style lang="scss">
.swaps-inputs {
  flex-grow: 1;
  display: flex;
  flex-direction: column;

  .swaps-item-table {
    flex-grow: 1;
  }
}
</style>
